<template>
  <div class="app-container">
    <h1></h1>
    <form @submit.prevent="updatePreferences">
      <div class="form-group">
        <label for="font-size">:</label>
        <select v-model="preferences.fontsize" id="font-size">
          <option value="small"></option>
          <option value="medium"></option>
          <option value="large"></option>
        </select>
      </div>
      <div class="form-group">
        <label for="notification-settings">:</label>
        <select v-model="preferences.notificationSettings" id="notification-settings">
          <option value="on"></option>
          <option value="off"></option>
        </select>
      </div>
      <div class="form-group">
        <label for="auto-login">:</label>
        <select v-model="preferences.autoLogin" id="auto-login">
          <option value="on"></option>
          <option value="off"></option>
        </select>
      </div>
      <div class="btn-container">
        <button type="submit" class="btn"></button>
        <button type="button" class="btn" @click="showAddModal"></button>
        <button type="button" class="btn btn-delete" @click="deletePreference"></button>
        <button type="button" class="btn btn-cancel" @click="resetPreferences"></button>
      </div>
    </form>
  
    <div v-if="successMessage" class="success-message"></div>
  
      <!--  -->
    <div v-if="showModal" class="modal">
      <div class="modal-content">
        <span class="close" @click="closeAddModal">&times;</span>
        <h2></h2>
        <form @submit.prevent="addPreference">
          <div class="form-group">
            <label for="add-font-size">:</label>
            <select v-model="newPreference.fontsize" id="add-font-size">
              <option value="small"></option>
              <option value="medium"></option>
              <option value="large"></option>
            </select>
          </div>
          <div class="form-group">
            <label for="add-notifications">:</label>
            <select v-model="newPreference.notificationSettings" id="add-notifications">
              <option value="on"></option>
              <option value="off"></option>
            </select>
          </div>
          <div class="form-group">
            <label for="add-auto-login">:</label>
            <select v-model="newPreference.autoLogin" id="add-auto-login">
              <option value="on"></option>
              <option value="off"></option>
            </select>
          </div>
          <div class="btn-container">
            <button type="submit" class="btn"></button>
            <button type="button" class="btn btn-cancel" @click="closeAddModal"></button>
          </div>
        </form>
      </div>
    </div>
  
      <!--  -->
    <h2></h2>
    <form @submit.prevent="queryPreferences">
      <div class="form-group">
        <label for="query-font-size">:</label>
        <select v-model="query.fontsize" id="query-font-size">
          <option value="">----</option>
          <option value="small"></option>
          <option value="medium"></option>
          <option value="large"></option>
        </select>
      </div>
      <div class="form-group">
        <label for="query-notifications">:</label>
        <select v-model="query.notificationSettings" id="query-notifications">
          <option value="">----</option>
          <option value="on"></option>
          <option value="off"></option>
        </select>
      </div>
      <div class="form-group">
        <label for="query-auto-login">:</label>
        <select v-model="query.autoLogin" id="query-auto-login">
          <option value="">----</option>
          <option value="on"></option>
          <option value="off"></option>
        </select>
      </div>
      <div class="btn-container">
        <button type="submit" class="btn"></button>
      </div>
    </form>
  
      <!--  -->
    <div class="results" v-if="queryResults.length > 0">
      <table>
        <thead>
          <tr>
            <th>ID</th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="result in queryResults" :key="result.user_id">
            <td>{{ result.user_id }}</td>
            <td>{{ result.fontsize }}</td>
            <td>{{ result.notificationSettings === 'on' ? '' : '' }}</td>
            <td>{{ result.autoLogin === 'on' ? '' : '' }}</td>
          </tr>
        </tbody>
      </table>
    </div>
    <div v-else class="results"></div>
  </div>
</template>

<script>
import axios from 'axios'; //  axios

export default {
  name: 'PersonalPreference',
  data() {
    return {
      preferences: {
        user_id: null,  //  user_id
        fontsize: '',
        notificationSettings: 'off',
        autoLogin: 'off'
      },
      newPreference: {
        fontsize: 'medium',
        notificationSettings: 'off',
        autoLogin: 'off'
      },
      query: {
        fontsize: '',
        notificationSettings: '',
        autoLogin: ''
      },
      queryResults: [],
      successMessage: false,
      showModal: false,
      isSidebarActive: false
    };
  },
  created() {
    this.loadPreferences();
  },
  methods: {
    loadPreferences() {
      axios.get('/preferences/get_preferences/')
        .then(response => {
          this.preferences = response.data;
        })
        .catch(error => {
          console.error(':', error);
        });
    },
    updatePreferences() {
      axios.post('/preferences/update_preference/', this.preferences)
        .then(response => {
          if (response.data.status === 'success') {
            this.showSuccessMessage();
          } else {
            alert('');
          }
        })
        .catch(error => {
          console.error(':', error);
        });
    },
    showAddModal() {
      this.showModal = true;
    },
    closeAddModal() {
      this.showModal = false;
    },
    addPreference() {
      axios.post('/preferences/add_preference/', this.newPreference)
        .then(response => {
          if (response.data.status === 'success') {
            this.closeAddModal();
            this.showSuccessMessage();
          } else {
            alert('');
          }
        })
        .catch(error => {
          console.error(':', error);
        });
    },
    deletePreference() {
      axios.post('/preferences/delete_preference/', { user_id: this.preferences.user_id })  //  user_id 
        .then(response => {
          if (response.data.status === 'success') {
            this.showSuccessMessage();
          } else {
            alert('');
          }
        })
        .catch(error => {
          console.error(':', error);
        });
    },
    resetPreferences() {
      this.loadPreferences();
    },
    queryPreferences() {
      let query = `/preferences/query_preferences/?`;
      if (this.query.fontsize) query += `fontsize=${this.query.fontsize}&`;
      if (this.query.notificationSettings) query += `notificationSettings=${this.query.notificationSettings}&`;
      if (this.query.autoLogin) query += `autoLogin=${this.query.autoLogin}&`;

      axios.get(query)
        .then(response => {
          this.queryResults = response.data;
        })
        .catch(error => {
          console.error(':', error);
        });
    },
    showSuccessMessage() {
      this.successMessage = true;
      setTimeout(() => {
        this.successMessage = false;
      }, 3000);
    }
  }
};
</script>

<style scoped src="@/assets/css/backend/PersonalPreference.css"></style>
