<template>
  <div class="dashboard-container">
    <div class="header">
      <h2></h2>
      <div class="chart-actions">
        <button @click="openAddChartModal"></button>
        <button @click="openEditChartModal"></button>
      </div>
    </div>

    <div class="selection-container">
      <div class="module-selection">
        <label for="module-select">:</label>
        <select id="module-select" v-model="selectedModule" @change="onModuleChange">
          <option v-for="module in modules" :key="module.id" :value="module.id">{{ module.name }}</option>
        </select>
      </div>

      <div class="role-selection">
        <label for="role-select">:</label>
        <select id="role-select" v-model="selectedRole" @change="onRoleChange">
          <option v-for="role in roles" :key="role.id" :value="role.id">{{ role.name }}</option>
        </select>
      </div>
    </div>

    <div class="chart-selection">
      <button @click="setCurrentChart('SalesChart')"></button>
      <button @click="setCurrentChart('RevenueChart')"></button>
      <button @click="setCurrentChart('InventoryChart')"></button>
    </div>

    <div class="chart-container">
      <draggable v-model="charts" class="drag-area" :options="{ group: 'charts', animation: 200 }">
        <div v-for="chart in charts" :key="chart.id" class="chart-item">
          <component :is="chart.component" :role-id="selectedRole" />
        </div>
      </draggable>
    </div>

    <!--  -->
    <Modal v-if="showAddChartModal" @close="closeAddChartModal">
      <template #header>
        <h3></h3>
      </template>
      <template #body>
        <form @submit.prevent="submitAddChartForm">
          <div>
            <label for="chartName">:</label>
            <input type="text" id="chartName" v-model="newChart.name" required>
          </div>
          <button type="submit"></button>
        </form>
      </template>
    </Modal>

    <!--  -->
    <Modal v-if="showEditChartModal" @close="closeEditChartModal">
      <template #header>
        <h3></h3>
      </template>
      <template #body>
        <form @submit.prevent="submitEditChartForm">
          <div>
            <label for="chartName">:</label>
            <input type="text" id="chartName" v-model="editChart.name" required>
          </div>
          <button type="submit"></button>
        </form>
      </template>
    </Modal>
  </div>
</template>

<script>
import draggable from 'vuedraggable';
import SalesChart from '@/Charts/SalesChart.vue';
import RevenueChart from '@/Charts/RevenueChart.vue';
import InventoryChart from '@/Charts/InventoryChart.vue';
import Modal from '@/components/backend/ChartModal.vue'; //  Modal 
import axios from '@/axios';

export default {
  name: 'DashboardManager',
  components: {
    draggable,
    SalesChart,
    RevenueChart,
    InventoryChart,
    Modal,
  },
  data() {
    return {
      currentChart: 'SalesChart',
      modules: [],           // 
      roles: [],             // 
      selectedModule: null,  // 
      selectedRole: null,    // 
      showAddChartModal: false,
      showEditChartModal: false,
      charts: [
        { id: 1, component: 'SalesChart' },
        { id: 2, component: 'RevenueChart' },
        { id: 3, component: 'InventoryChart' }
      ], // 
      newChart: {
        name: '',
      },
      editChart: {
        name: '',
      },
    };
  },
  methods: {
    async fetchModules() {
      try {
        const response = await axios.get('/api/backend/modules/');
        this.modules = response.data;
        this.selectedModule = this.modules.length > 0 ? this.modules[0].id : null;
      } catch (error) {
        console.error('Error fetching modules:', error);
      }
    },
    async fetchRoles() {
      try {
        const response = await axios.get(`/api/backend/get_roles_by_module/${this.selectedModule}/`);
        this.roles = response.data;
        this.selectedRole = this.roles.length > 0 ? this.roles[0].id : null;
      } catch (error) {
        console.error('Error fetching roles:', error);
      }
    },
    setCurrentChart(chart) {
      this.currentChart = chart;
    },
    onModuleChange() {
      this.fetchRoles();  // 
      console.log(':', this.selectedModule);
    },
    onRoleChange() {
      console.log(':', this.selectedRole);
    },
    openAddChartModal() {
      this.showAddChartModal = true;
    },
    closeAddChartModal() {
      this.showAddChartModal = false;
    },
    submitAddChartForm() {
      console.log(':', this.newChart);
      this.closeAddChartModal();
    },
    openEditChartModal() {
      this.showEditChartModal = true;
    },
    closeEditChartModal() {
      this.showEditChartModal = false;
    },
    submitEditChartForm() {
      console.log(':', this.editChart);
      this.closeEditChartModal();
    },
  },
  async mounted() {
    await this.fetchModules(); // 
    await this.fetchRoles();   // 
  },
};
</script>

<style scoped src="@/assets/css/backend/Dashboard.css"></style>